.main-nav__close {
  position: absolute;
  right: 35px;
  top: 9px;
  z-index: 2;
  display: block;
  width: 20px;
  height: 50px;
  cursor: pointer;
  border: none;
  background-color: inherit;
}
.main-nav__close::before,
.main-nav__close::after {
  content: "";
  position: absolute;
  top: 17px;
  left: 20px;
  width: 23px;
  height: 3px;
  background-color: #ffffff;
}
.main-nav__close::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav__close::after {
  transform: rotate(-45deg);
}
.main-nav__close:active::before,
.main-nav__close:active::after {
  background-color: rgba(255, 255, 255, 0.3);
}
@media (min-width: 768px) {
  .main-nav__close {
    display: none;
  }
}
@media (min-width: 1200px) {
  .main-nav__close {
    display: none;
  }
}
.main-nav__toggle {
  position: absolute;
  right: 0;
  top: 0;
  z-index: 2;
  display: block;
  width: 58px;
  height: 50px;
  cursor: pointer;
  border: none;
  background-color: #FFFFFF;
}
.main-nav__toggle:hover {
  opacity: 0.6;
}
.main-nav__toggle:active {
  opacity: 0.3;
}
.main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 15px;
  left: 17px;
  width: 23px;
  height: 4px;
  background-color: #81B3D3;
  box-shadow: 0 8px 0 0 #81B3D3, 0 16px 0 0 #81B3D3;
}
