.form__mark [type="radio"] {
  position: absolute;
  right: 100vw;
}
.form__mark [type="radio"] + label {
  display: inline-block;
  position: relative;
  margin: 0 4px;
  width: 48px;
  height: 48px;
  border-radius: 50%;
  cursor: pointer;
}
.form__mark [type="radio"] + label:nth-of-type(1) {
  background: #F2F2F2;
}
.form__mark [type="radio"] + label:nth-of-type(2) {
  background: #F2F2F2;
}
.form__mark [type="radio"] + label:nth-of-type(3) {
  background: #F2F2F2;
}
.form__mark [type="radio"] + label::before {
  position: absolute;
  top: 2px;
  right: 0;
  bottom: 0;
  left: 14px;
  opacity: 0;
  background: url(../img/black-circle.svg) 1% no-repeat;
  transition: all 0.3s ease;
  content: "";
}
.form__mark [type="radio"]:checked + label::before {
  opacity: 1;
}
.form__check {
  text-align: left;
}
.form__check [type="checkbox"] {
  position: absolute;
  right: 100vw;
}
.form__check [type="checkbox"] + label {
  display: inline-block;
  position: relative;
  margin: 0 4px;
  width: 48px;
  height: 48px;
  border-radius: 10%;
  cursor: pointer;
}
.form__check [type="checkbox"] + label:nth-of-type(1) {
  background: #F2F2F2;
}
.form__check [type="checkbox"] + label:nth-of-type(2) {
  background: #F2F2F2;
}
.form__check [type="checkbox"] + label:nth-of-type(3) {
  background: #F2F2F2;
}
.form__check [type="checkbox"] + label:nth-of-type(4) {
  background: #F2F2F2;
}
.form__check [type="checkbox"] + label::before {
  position: absolute;
  top: 2px;
  right: 0;
  bottom: 0;
  left: 14px;
  opacity: 0;
  background: url(../img/check-mark.svg) 1% no-repeat;
  transition: all 0.3s ease;
  content: "";
}
.form__check [type="checkbox"]:checked + label::before {
  opacity: 1;
}
